<template>
  <el-collapse-item name="9" title="">
    <!--  -->
    <el-row>
      <el-col :span="2">
        <i class="el-icon-menu"></i>
      </el-col>
      <el-col :span="10"></el-col>
      <el-col :span="12">
        <el-select size="mini" v-model="seriesValue">
          <el-option
            :key="i"
            :label="item"
            :value="item"
            v-for="(item, i) in seriesOption"
          ></el-option>
        </el-select>
      </el-col>
    </el-row>
    <!--  -->
    <el-row>
      <el-col :span="6"></el-col>
      <el-col :span="12">
        <el-select size="mini" v-model="series.type" @change="exType">
          <el-option
            :key="i"
            :label="item.label"
            :value="item.value"
            v-for="(item, i) in typeOption"
          ></el-option>
        </el-select>
      </el-col>
    </el-row>

    <!--  -->
    <chart-base-radio
      :radioValue.sync="series.yradio"
      :radioOption="radioOption"
      :prop="'yradio'"
      @summit="summit(arguments)"
    >
      <div slot="title">Y</div>
    </chart-base-radio>
    <chart-base-radio
      :radioValue.sync="series.xradio"
      :radioOption="radioOption"
      :prop="'xradio'"
      @summit="summit(arguments)"
    >
      <div slot="title">X</div>
    </chart-base-radio>

    <!--  -->
    <div v-show="series.type == 'line'">
      <el-row style="margin-top: 15px">
        <el-col :span="3"></el-col>
        <!--  -->
        <el-col :span="9">
          <chart-base-select
            :hideCol="true"
            :selectValue.sync="series.lineWidth"
            :selectOption="lineWidthOption"
            :prop="'lineWidth'"
            @summit="summit(arguments)"
          ></chart-base-select>
        </el-col>
        <!--  -->
        <el-col :span="9">
          <chart-base-select
            :hideCol="true"
            :selectValue.sync="series.lineType"
            :selectOption="lineStyleOption"
            :prop="'lineType'"
            @summit="summit(arguments)"
          ></chart-base-select>
        </el-col>
        <el-col :span="3">
          <el-color-picker
            size="mini"
            v-model="series.lineColor"
          ></el-color-picker>
        </el-col>
      </el-row>
      <!--  -->
      <chart-base-box
        :boxData.sync="series.lineStyle"
        :checkboxOption="lineTypeOption"
        :showCol="true"
        :prop="'lineStyle'"
        @summit="summit(arguments)"
      >
        <div slot="title"></div>
      </chart-base-box>

      <!--  -->
      <chart-base-slider
        v-if="series.lineWidth == 'custom'"
        :baseSliderOption.sync="series.cuslineWidth"
        :unit="'px'"
        :content="''"
        :prop="'cuslineWidth'"
        @summit="summit(arguments)"
      ></chart-base-slider>

      <!--  -->
      <chart-base-switch
        :switchValue.sync="series.showSymbol"
        :prop="'showSymbol'"
        @summit="summit(arguments)"
      >
        <div slot="title"></div>
      </chart-base-switch>

      <!--  -->
      <el-row style="margin-top: 15px">
        <el-col :span="4"></el-col>
        <el-col :span="8">
          <chart-base-select
            :selectValue.sync="series.symbolSize"
            :prop="'symbolSize'"
            @summit="summit(arguments)"
            :selectOption="symbolSizeOption"
          ></chart-base-select>
        </el-col>
        <el-col :span="8">
          <chart-base-select
            :selectValue.sync="series.symbol"
            :prop="'symbol'"
            @summit="summit(arguments)"
            :selectOption="symbolOption"
          ></chart-base-select>
        </el-col>
        <el-col :span="3">
          <el-color-picker
            size="mini"
            v-model="series.itemColor"
          ></el-color-picker>
        </el-col>
      </el-row>
      <!--  -->
      <chart-base-slider
        v-if="series.symbolSize == 'custom'"
        :baseSliderOption.sync="series.cusSymbolSize"
        :unit="'px'"
        :prop="'cusSymbolSize'"
        @summit="summit(arguments)"
        :content="''"
      ></chart-base-slider>
    </div>
    <!--  -->
    <div v-show="series.type == 'bar'">
      <el-row style="margin-top: 15px">
        <el-col :span="6"></el-col>
        <el-col :span="3">
          <el-color-picker
            size="mini"
            v-model="series.barColor"
          ></el-color-picker>
        </el-col>
      </el-row>
      <!--  -->
      <chart-base-select
        :prop="'barWidth'"
        @summit="summit(arguments)"
        :selectOption="barWidthOption"
        :selectValue.sync="series.barWidth"
      >
        <div slot="select"></div>
      </chart-base-select>

      <chart-base-slider
        v-if="series.barWidth == 'custom'"
        :baseSliderOption.sync="series.cusbarWidth"
        :unit="'px'"
        :prop="'cusbarWidth'"
        @summit="summit(arguments)"
        :content="''"
      ></chart-base-slider>

      <!--  -->
      <chart-base-select
        :selectOption="barMinHeightOption"
        :prop="'barMinHeight'"
        @summit="summit(arguments)"
        :selectValue.sync="series.barMinHeight"
      >
        <div slot="select"></div>
      </chart-base-select>
      <chart-base-slider
        v-if="series.barMinHeight == 'custom'"
        :baseSliderOption.sync="series.cusbarMinHeight"
        :unit="'px'"
        :prop="'cusbarMinHeight'"
        @summit="summit(arguments)"
        :content="''"
      ></chart-base-slider>
      <!--  -->
      <chart-base-select
        :selectOption="barGapOption"
        :selectValue.sync="series.barGap"
        :prop="'barGap'"
        @summit="summit(arguments)"
        v-show="!chart_style.includes('stack')"
      >
        <div slot="select"></div>
      </chart-base-select>

      <chart-base-slider
        v-if="series.barGap == 'custom'"
        :baseSliderOption.sync="series.cusbarGap"
        :unit="'%'"
        :min="-100"
        :prop="'cusbarGap'"
        @summit="summit(arguments)"
        :content="''"
      ></chart-base-slider>
      <!--  -->
      <chart-base-select
        :selectOption="barCategoryGapOption"
        :selectValue.sync="series.barCategoryGap"
        :prop="'barCategoryGap'"
        @summit="summit(arguments)"
        v-show="!chart_style.includes('stack')"
      >
        <div slot="select"></div>
      </chart-base-select>

      <chart-base-slider
        v-if="series.barCategoryGap == 'custom'"
        :baseSliderOption.sync="series.cusbarCategoryGap"
        :unit="'%'"
        :min="-100"
        :prop="'cusbarCategoryGap'"
        @summit="summit(arguments)"
        :content="''"
      ></chart-base-slider>
    </div>
    <!--  -->
    <chart-base-switch
      :switchValue.sync="series.showLabel"
      :prop="'showLabel'"
      @summit="summit(arguments)"
    >
      <div slot="title"></div>
    </chart-base-switch>

    <div v-show="series.showLabel">
      <!--  -->
      <chart-base-select
        :selectOption="formatterOption"
        :selectValue.sync="series['format-format']"
        :prop="'format-format'"
        @summit="summit(arguments)"
      >
        <div slot="select"></div>
      </chart-base-select>
      <!--  -->
      <chart-base-select
        :selectOption="digitOption"
        :prop="'format-digit'"
        @summit="summit(arguments)"
        :selectValue.sync="series['format-digit']"
      >
        <div slot="select"></div>
      </chart-base-select>
      <!--  -->
      <chart-base-select
        :prop="'format-ratio'"
        @summit="summit(arguments)"
        :selectOption="ratioOption"
        :selectValue.sync="series['format-ratio']"
      >
        <div slot="select"></div>
      </chart-base-select>
      <!--  -->
      <chart-base-input
        :type="'text'"
        :prop="'format-prefix'"
        @summit="summit(arguments)"
        :inputValue.sync="series['format-prefix']"
        :placeholder="''"
      >
        <div slot="input"></div>
      </chart-base-input>
      <!--  -->
      <chart-base-input
        :type="'text'"
        :prop="'format-suffix'"
        @summit="summit(arguments)"
        :inputValue.sync="series['format-suffix']"
        :placeholder="''"
      >
        <div slot="input"></div>
      </chart-base-input>

      <el-row style="margin-top: 15px">
        <!--  -->
        <el-col :span="7">
          <chart-base-select
            :hideCol="true"
            :selectOption="textPosOption"
            :prop="'textPos'"
            @summit="summit(arguments)"
            :selectValue.sync="series.textPos"
            :tooltip="''"
          ></chart-base-select>
        </el-col>
        <!--  -->
        <el-col :span="7">
          <el-checkbox-group
            size="mini"
            v-model="series.fontPlace"
            :prop="'fontPlace'"
            @summit="summit(arguments)"
          >
            <el-checkbox-button label="bold">B</el-checkbox-button>
            <el-checkbox-button label="italic" style="fontstyle: italic"
              >I</el-checkbox-button
            >
          </el-checkbox-group>
        </el-col>
        <!--  -->
        <el-col :span="7">
          <chart-base-select
            :hideCol="true"
            :prop="'fontSize'"
            @summit="summit(arguments)"
            :selectOption="fontSizeOption"
            :selectValue.sync="series.fontSize"
            :tooltip="''"
          ></chart-base-select>
        </el-col>
        <el-col :span="3">
          <el-color-picker
            :label="true"
            size="mini"
            v-model="series.fzColor"
          ></el-color-picker>
        </el-col>
      </el-row>
      <!--  -->
      <div v-show="series.textPos == 'custom'">
        <chart-base-slider
          :baseSliderOption.sync="series.offsetX"
          :unit="'%'"
          :content="''"
          :max="500"
          :min="-200"
          :prop="'offsetX'"
          @summit="summit(arguments)"
        ></chart-base-slider>
        <chart-base-slider
          :baseSliderOption.sync="series.offsetY"
          :unit="'%'"
          :content="''"
          :max="200"
          :min="-200"
          :prop="'offsetY'"
          @summit="summit(arguments)"
        ></chart-base-slider>
      </div>
      <!--  -->
      <chart-base-slider
        v-if="series.fontSize == 'custom'"
        :baseSliderOption.sync="series.customSize"
        :prop="'customSize'"
        @summit="summit(arguments)"
        :unit="'px'"
        :content="''"
      ></chart-base-slider>
    </div>
    <!--  -->
    <div>
      <!--  -->
      <chart-base-switch
        :switchValue.sync="series.showStack"
        :prop="'showStack'"
        @summit="summit(arguments)"
      >
        <div slot="title"></div>
      </chart-base-switch>

      <div v-show="series.showStack">
        <!--  -->
        <chart-base-input
          :type="'text'"
          :inputValue.sync="series.stackValue"
          :placeholder="''"
          :prop="'stackValue'"
          @summit="summit(arguments)"
        >
          <div slot="input"></div>
        </chart-base-input>
        <!--  -->
        <chart-base-select
          v-show="series.type == 'bar'"
          :selectOption="barCategoryGapOption"
          :selectValue.sync="series.barCategoryGap"
          :prop="'barCategoryGap'"
          @summit="summit(arguments)"
        >
          <div slot="select"></div>
        </chart-base-select>
        <!--  -->
        <chart-base-slider
          v-if="series.barCategoryGap == 'custom'"
          :baseSliderOption.sync="series.cusbarCategoryGap"
          :unit="'%'"
          :min="-100"
          :prop="'cusbarCategoryGap'"
          @summit="summit(arguments)"
          :content="''"
        ></chart-base-slider>
      </div>
    </div>
    <div v-show="series.ype == 'line'">
      <!--  -->
      <chart-base-switch :switchValue.sync="show">
        <div slot="title"></div>
      </chart-base-switch>

      <el-row style="margin-top: 15px" v-show="show">
        <el-col :span="6" class="title"></el-col>
        <el-col :span="18">
          <el-color-picker
            size="mini"
            v-model="series.areaColor"
          ></el-color-picker>
        </el-col>
      </el-row>
    </div>
  </el-collapse-item>
</template>

<script>
import * as t from "@/utils/importUtil";
import {
  lineStyleOption,
  symbolSizeOption,
  symbolOption,
  ratioOption,
  digitOption,
  fontSizeOption,
} from "@/data/chartJson";

export default {
  props: {
    chartAllType: String,
    seriesOptionData: Object,
    router: String,
    lang: {
      type: String,
      default: "zh",
    },
  },
  data() {
    return {
      seriesData: {},
      radioOption: [
        {
          label: 0,
          text: "",
        },
        {
          label: 1,
          text: "",
        },
      ],
      lineTypeOption: {
        step: {
          text: "",
          des: "",
        },
        smooth: {
          text: "",
          des: "",
        },
      },
      series: {},
      prop: null,
      lineWidthOption: [
        { value: 1, label: "1px" },
        { value: 2, label: "2px" },
        { value: 3, label: "3px" },
        { value: 4, label: "4px" },
        { value: 5, label: "5px" },
        { value: 6, label: "6px" },
        { value: 7, label: "7px" },
        { value: 8, label: "8px" },
        { value: "custom", label: "" },
      ],
      lineStyleOption: "",
      symbolSizeOption: "",
      symbolOption: "",
      barWidthOption: [
        { value: null, label: "" },
        { value: 10, label: "" },
        { value: 30, label: "" },
        { value: 50, label: "" },
        { value: "custom", label: "" },
      ],
      barMinHeightOption: [
        { value: 0, label: "" },
        { value: 5, label: "" },
        { value: 10, label: "" },
        { value: 30, label: "" },
        { value: "custom", label: "" },
      ],
      barGapOption: [
        { value: "30%", label: "" },
        { value: "50%", label: "" },
        { value: "40%", label: "" },
        { value: "20%", label: "" },
        { value: "0%", label: "" },
        { value: "-10%", label: "" },
        { value: "custom", label: "" },
      ],
      ratioOption: "",
      digitOption: "",
      textPosOption: [
        { value: "top", label: "" },
        { value: "left", label: "" },
        { value: "right", label: "" },
        { value: "bottom", label: "" },
        { value: "inside", label: "" },
        { value: "insideLeft", label: "" },
        { value: "insideRight", label: "" },
        { value: "insideTop", label: "" },
        { value: "insideBottom", label: "" },
        { value: "insideTopLeft", label: "" },
        { value: "insideBottomLeft", label: "" },
        { value: "insideTopRight", label: "" },
        { value: "insideBottomRight", label: "" },
        { value: "custom", label: "" },
      ],
      fontSizeOption: "",
      alignOption: [
        { value: "left", label: "" },
        { value: "center", label: "" },
        { value: "right", label: "" },
      ],
      verticalAlignOption: [
        { value: "top", label: "" },
        { value: "middle", label: "" },
        { value: "bottom", label: "" },
        { value: "custom", label: "" },
      ],
      barCategoryGapOption: [
        { value: "20%", label: "" },
        { value: "40%", label: "" },
        { value: "30%", label: "" },
        { value: "10%", label: "" },
        { value: "custom", label: "" },
      ],
      radiusOption: [
        { value: 0, label: "" },
        { value: "custom", label: "" },
      ],
    };
  },
  components: {
    ...t.importComp(t),
  },
  watch: {
    seriesOptionData: {
      handler(newVal, oldVal) {
        if (t.isEqual(this.seriesData, this.seriesOptionData)) {
          return;
        }
        this.seriesData = t.deepCopy(this.seriesOptionData);
        // name
        if (!Reflect.has(this.seriesData.option[0], "name")) {
          let data = this.chartLists[this.currentChartIndex].chartOptions
            .defaultOption.legend.data;

          this.seriesData.option[0].name = "";
          let type = this.chartLists[this.currentChartIndex].chartOptions
            .defaultOption.type;
          if (type == "area") {
            type = "line";
          } else if (type == "column") {
            type = "bar";
          }
          this.seriesData.option[0].type = type;
          // 
          for (let i = 0; i < data.length; i++) {
            let option = t.deepCopy(this.seriesData.option[0]);
            option.name = data[i];
            this.seriesData.option.push(option);
          }
        }

        this.series = this.seriesData.option[this.seriesData.currentIndex];
        this.lineStyleOption = t.deepCopy(lineStyleOption);
        this.symbolSizeOption = t.deepCopy(symbolSizeOption);
        this.symbolOption = t.deepCopy(symbolOption);
        this.ratioOption = t.deepCopy(ratioOption);
        this.digitOption = t.deepCopy(digitOption);
        this.fontSizeOption = t.deepCopy(fontSizeOption);
      },
      immediate: true,
      deep: true,
    },
    series: {
      handler: function (newVal, oldVal) {
        // 
        if (oldVal) {
          this.changeSeries();
        }
      },
      deep: true,
      immediate: true,
    },
  },
  computed: {
    ...t.mapState("chartSetting", ["chartLists", "currentChartIndex"]),
    seriesOption() {
      let arr = [];
      if (this.seriesData.option) {
        for (let i = 0; i < this.seriesData.option.length; i++) {
          arr[i] = this.seriesData.option[i].name;
        }
      }
      return arr;
    },
    seriesValue: {
      get() {
        return this.seriesOption[this.seriesData.currentIndex];
      },
      set(val) {
        this.seriesData.currentIndex = this.seriesOption.findIndex(
          (item) => item == val
        );
        this.series = this.seriesData.option[this.seriesData.currentIndex];
      },
    },
    chart_pro: function () {
      return this.chartAllType.split("|")[0];
    },
    chart_type() {
      return this.chartAllType.split("|")[1];
    },
    chart_style() {
      return this.chartAllType.split("|")[2];
    },
    typeOption() {
      return [
        { value: "bar", label: "" },
        { value: "line", label: "" },
      ];
    },
    formatterOption() {
      return [
        { value: "{c}", label: "" },
        { value: "{bc}", label: "+" },
      ];
    },

    // ,
    show: {
      get() {
        let flag = this.chartLists[this.currentChartIndex].chartOptions
          .defaultOption.area;
        return flag;
      },
      set(val) {
        this.chartLists[
          this.currentChartIndex
        ].chartOptions.defaultOption.area = val;
      },
    },
  },
  methods: {
    ...t.mapActions("chartSetting", ["updateChartItem",'updateCurrentProp']),
    changeSeries() {
      // 
      let prop = {
        prop: 'commonSeries:' + this.prop,
        oldValue: this.oldVal,
        value: this.curVal,
        index: this.seriesData.currentIndex
      }

      const updateObj = {
        updateObj: t.deepCopy(this.seriesData),
        router: this.router,
      };
      this.updateChartItem(updateObj);
      this.updateCurrentProp(prop)
    },
    exType(){
      this.prop = 'type'
      this.changeSeries()
    },
    summit(val) {
      this.prop = val[0];
      this.curVal = val[1]
      this.oldVal = val[2]
    },
  },
};
</script>

<style>
</style>
