const partComponent = {
    label: function (product, type, style, comp) {
        return {
            fontSize: null,
            fontStyle: "normal",
            color: "#333",
            fontWeight: "normal",
            // fontFamily: null,
            fontPlace: "horizen", //horizenvertical
            textBorderColor: null
        };
    },
    //,echartshighcharts
    border: function (product, type, style, comp) {
        return {};
    },
    //,echartshighcharts
    position: function (product, type, style, comp) {
        return {};
    },
    //echartsitemStylehighchartsmarker
    item: function (product, type, style, comp) {
        return {
            color: null,
            barderColor: "#000",
            borderWidth: 1,
            borderType: "solid"
        };
    },
    // 
    grid: function (product, type, style) {
        return {
            top: "15%",
            left: "15%",
            right: "15%",
            bottom: "15%"
        };
    },
    //echartslineStylehighcharts
    line: function (product, type, style, comp) {
        return {
            color: "#333",
            width: 1,
            type: "solid"
        };
    },
    axis: function (product, type, style, comp) {
        return {
            axisLine: {
                show: true,
                lineStyle: {
                    color: "#333",
                    width: 1
                }
            },
            axisTick: {
                show: true,
                inside: false,
                length: 5,
                lineStyle: {
                    color: "#999999",
                    width: 1
                }
            },
            axisLabel: {
                show: true,
                rotate: 0
            },
            splitLine: {
                show: false,
                interval: "auto",
                lineStyle: {
                    width: 1,
                    type: "solid",
                    color: "#ccc"
                }
            },
            splitArea: {
                show: false,
                interval: "auto",
                areaStyle: {
                    color: ["rgba(250,250,250,0.3)", "rgba(200,200,200,0.3)"]
                }
            }
        };
    },
    // 
    config: function (product, type, style) {
        return {
            grid: partComponent.grid(product, type, style),
            color: null,
            fontFamily: "Sans-serif"
        };
    },
    //x
    xAxis: function (product, type, style) {
        return [{
            show: true,
            type: 'category',
            axisLabel: partComponent.label(product, type, style),
            axisLine: partComponent.axis(product, type, style).axisLine,
            axisTick: partComponent.axis(product, type, style).axisTick,
            nameTextStyle: partComponent.label(product, type, style),
            splitLine: partComponent.axis(product, type, style).splitLine,
            splitArea: partComponent.axis(product, type, style).splitArea
        }, {
            show: false,
            type: 'category',
            label: partComponent.label(product, type, style),
            axisLine: partComponent.axis(product, type, style).axisLine,
            axisTick: partComponent.axis(product, type, style).axisTick,
            nameTextStyle: partComponent.label(product, type, style),
            splitLine: partComponent.axis(product, type, style).splitLine,
            splitArea: partComponent.axis(product, type, style).splitArea
        }];
    },
    //y
    yAxis: function (product, type, style) {
        return [
            {
                show: true,
                type: 'value',
                label: partComponent.label(product, type, style),
                axisLine: partComponent.axis(product, type, style).axisLine,
                axisTick: partComponent.axis(product, type, style).axisTick,
                nameTextStyle: partComponent.label(product, type, style),
                splitLine: partComponent.axis(product, type, style).splitLine,
                splitArea: partComponent.axis(product, type, style).splitArea
            },
            {
                show: false,
                type: 'category',
                label: partComponent.label(product, type, style),
                axisLine: partComponent.axis(product, type, style).axisLine,
                axisTick: partComponent.axis(product, type, style).axisTick,
                nameTextStyle: partComponent.label(product, type, style),
                splitLine: partComponent.axis(product, type, style).splitLine,
                splitArea: partComponent.axis(product, type, style).splitArea
            }
        ];
    },
    //
    title: function (product, type, style) {
        return {
            show: false,
            text: "",
            subtext: "",
            textStyle: partComponent.label(product, type, style)
        };
    },
    //
    subtitle: function (product, type, style) {
        return {};
    },
    //
    legend: function (product, type, style) {
        return {
            show: false,
            textStyle: partComponent.label(product, type, style),
            itemGap: 10,
            orient: "horizontal",
            itemStyle: {},
            itemWidth: 25,
            itemHeight: 14,
            x: 0,
            y: 0
        };
    },
    //
    tooltip: function (product, type, style) {
        var label = deepCopy(
            partComponent.label(product, type, style, "tooltip")
        );
        label.color = "#fff";

        return {
            show: true,
            textStyle: label,
            triggerOn: "mousemove",
            trigger: "item",
            axisPointer: {
                lineStyle: {
                    color: "#555",
                    width: 1,
                    type: "solid"
                },
                crossStyle: {
                    lineStyle: {
                        color: "#555",
                        width: 1,
                        type: "solid"
                    }
                },
                shadowStyle: {
                    color: "rgba(150,150,150,0.3)"
                }
            },
            backgroundColor: 'rgba(50,50,50,0.7)'
        };
    },
}

